.cat_image {
  width: 400px;
  height: 300px;
}

.cat_text {
  width: 500px;
}

.cat-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.breed-select {
  color: #333;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  margin-bottom: 15px;
  padding: 10px;
  font-size: 16px;
}

.breed-select:hover {
  border-color: #888;
}

.breed-select:focus {
  border-color: #4d90fe;
  box-shadow: 0 0 5px rgba(77, 144, 254, .7);
}

.breed-select:active, .breed-select:checked {
  background-color: #f0f8ff;
  border-color: #2f5bb7;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: #de3500 rgba(0, 0, 0, 0) #fff;
  border-radius: 50%;
  animation: rotate 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border: 10px solid rgba(0, 0, 0, 0);
  border-bottom-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-10px, 19px)rotate(-35deg);
}

.loader:after {
  border-color: #de3500 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  transform: translate(32px, 3px)rotate(-35deg);
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

